import React from 'react';
import {BreadcrumbItem} from '@here/hds-breadcrumb';

new BreadcrumbItem();

interface HDSBreadcrumbItemProps extends React.HTMLAttributes<HTMLElement> {
  children: React.ReactNode;
  active?: boolean;
}
const HDSBreadcrumbItem = React.forwardRef(
  ({active, children, className, ...rest}: HDSBreadcrumbItemProps, ref) => (
    <hds-breadcrumb-item
      {...(active && {active})}
      {...(className && {class: className})}
      ref={ref}
      {...rest}
    >
      {children}
    </hds-breadcrumb-item>
  )
);

HDSBreadcrumbItem.defaultProps = {
  active: false,
};

export default HDSBreadcrumbItem;
